html {
  background-color: #F2F1F1;
}

.App {
  text-align: center;
}

.App-header {
  background-color: #4F58A8;
  height: 50px;
  width: 100%;
  padding-bottom: 30px;
  padding-top: 1px;
  color: white;
  display: -ms-inline-flexbox;
  display: inline-flex;
}

.head {
  background-color: #F2F1F1;;
  height: 50px;
  width: 100%;
  padding-bottom: 30px;
  padding-top: 1px;
  color: white;
  display: -ms-inline-flexbox;
  display: inline-flex;
}

.search {
  border: 10px solid white;
  font-size:1.2em;
  font-style: italic;
  padding:10px;
  height: 50px;
  width: 200px;
  -ms-flex-line-pack: left;
      align-content: left;
  margin-top: 15px;
  margin-left: 20px;
}

.create {
  height: 50px;
  width: 200px;
  font-size: 1.2em;
  font-style: italic;
  margin-top: 15px;
  margin-right: 20px;
}

.input_form {
  -ms-flex-line-pack: center;
      align-content: center;
  display: inline-block;
  height: 100%;
  width: 450px;
  border-style: solid;
  border-width: 4px;
  border-color: black;
}

.input_bar {
  display: block;
  margin-left: auto;
  margin-right: auto;
  margin-top: 5%;
  margin-bottom: 5%;
  height: 40px;
  width: 395px;
  border-style: solid;
  font-size: 1em;
  font-style: italic;
}

.submit {
  height: 40px;
  width: 100px;
  font-size: 1em;
}

.tooltip {
  position: absolute;
  font-size: 12px;
  width:  auto;
  height: auto;
  pointer-events: none;
  background-color: white;
}

.results {
  color: black;
}

.bubbleChart {
  display: block;
}

.numVisitsText {
  display: -ms-inline-flexbox;
  display: inline-flex;
  margin-top: 40px;
  font-size: 1.5em;
}

.visits1 {
  position: absolute;
  left: 235px;
}

.visits2 {
  position: absolute;
  left: 530px;
}

.visits3 {
  position: absolute;
  left: 820px;
}

.visits4 {
  position: absolute;
  left: 1130px;
}

/*begin toggle switch*/
.toggleSwitch {
  display: -ms-inline-flexbox;
  display: inline-flex;
  margin-top: 20px;
  margin-left: 40px;
  border-style: solid;
  background-color: #A0F798;
}

.viewText {
  display: inline-block;
  text-align: center;
  vertical-align: middle;
  line-height: 5px;
  margin-left: 5px;
  margin-right: 5px;
}


.switch {
  position: relative;
  display: inline-block;
  width: 60px;
  height: 38px;
}

.switch input {display:none;}

.slider {
  position: absolute;
  cursor: pointer;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: #ccc;
  -webkit-transition: .1s;
  -o-transition: .1s;
  transition: .1s;
}

.slider:before {
  position: absolute;
  content: "";
  height: 30px;
  width: 26px;
  left: 4px;
  bottom: 4px;
  background-color: white;
  -webkit-transition: .1s;
  -o-transition: .1s;
  transition: .1s;
}

input:checked + .slider {
  background-color: #F07565;
}

input:focus + .slider {
  -webkit-box-shadow: 0 0 1px #2196F3;
          box-shadow: 0 0 1px #2196F3;
}

input:checked + .slider:before {
  -webkit-transform: translateX(26px);
  -ms-transform: translateX(26px);
  transform: translateX(26px);
}
/*end toggle switch*/

.App-title {
  font-size: 3em;
  margin-top: 15px;
  margin-left: auto;
  margin-right: auto;
}


